import React, { useState, useEffect, useRef } from 'react';
import {
  TextField,
  Autocomplete,
  Box,
  Typography,
  Chip,
  Paper,
  IconButton,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Alert,
  Tooltip,
} from '@mui/material';
import { 
  Search as SearchIcon,
  Delete as DeleteIcon,
  History as HistoryIcon,
  TipsAndUpdates as TipsIcon,
} from '@mui/icons-material';
import { stockAPI } from '../utils/api';

const StockSearch = ({ value, onChange, onSelect }) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const [showHistory, setShowHistory] = useState(false);
  const [searchHistory, setSearchHistory] = useState([]);
  const [showTips, setShowTips] = useState(false);
  const searchTimeoutRef = useRef(null);

  // 智能搜索提示示例
  const searchTips = [
    { type: '股票代码', example: '000001、600036', desc: '直接输入6位股票代码' },
    { type: '公司名称', example: '平安银行、招商银行', desc: '输入完整或部分公司名称' },
    { type: '拼音搜索', example: 'pab、zsyh、zmj', desc: '使用公司名称拼音首字母' },
    { type: '模糊搜索', example: '银行、科技、地产', desc: '按行业或关键词搜索' },
  ];

  // 热门股票推荐（更新后的列表）
  const popularStocks = [
    { code: '000001', name: '平安银行', sector: '银行', tips: '可搜索：pab、平安' },
    { code: '000002', name: '万科A', sector: '房地产', tips: '可搜索：wk、万科' },
    { code: '600000', name: '浦发银行', sector: '银行', tips: '可搜索：pdfyh、浦发' },
    { code: '600036', name: '招商银行', sector: '银行', tips: '可搜索：zsyh、招商' },
    { code: '000858', name: '五粮液', sector: '白酒', tips: '可搜索：wly、五粮' },
    { code: '601717', name: '郑煤机', sector: '机械', tips: '可搜索：zmj、郑煤' },
  ];

  // 从localStorage加载搜索历史
  useEffect(() => {
    const savedHistory = localStorage.getItem('stockSearchHistory');
    if (savedHistory) {
      try {
        setSearchHistory(JSON.parse(savedHistory));
      } catch (error) {
        console.error('加载搜索历史失败:', error);
        setSearchHistory([]);
      }
    }
  }, []);

  // 保存搜索历史到localStorage
  const saveSearchHistory = (history) => {
    try {
      localStorage.setItem('stockSearchHistory', JSON.stringify(history));
    } catch (error) {
      console.error('保存搜索历史失败:', error);
    }
  };

  // 添加搜索历史
  const addToHistory = (stock) => {
    const newHistory = [
      stock,
      ...searchHistory.filter(item => item.code !== stock.code)
    ].slice(0, 10); // 最多保存10条记录
    
    setSearchHistory(newHistory);
    saveSearchHistory(newHistory);
  };

  // 删除搜索历史
  const removeFromHistory = (codeToRemove) => {
    const newHistory = searchHistory.filter(item => item.code !== codeToRemove);
    setSearchHistory(newHistory);
    saveSearchHistory(newHistory);
  };

  // 清空搜索历史
  const clearHistory = () => {
    setSearchHistory([]);
    localStorage.removeItem('stockSearchHistory');
  };

  // 智能搜索股票
  const searchStocks = async (query) => {
    if (!query.trim()) {
      setOptions([]);
      return;
    }

    setLoading(true);
    try {
      const response = await stockAPI.searchStocks(query, 8);
      if (response.success) {
        setOptions(response.stocks);
        console.log('搜索结果:', response.stocks); // 调试输出
      } else {
        setOptions([]);
      }
    } catch (error) {
      console.error('搜索失败:', error);
      setOptions([]);
    } finally {
      setLoading(false);
    }
  };

  // 处理输入变化
  const handleInputChange = (event, newInputValue) => {
    setInputValue(newInputValue);
    setShowHistory(false);
    setShowTips(false);
    
    // 清除之前的定时器
    if (searchTimeoutRef.current) {
      clearTimeout(searchTimeoutRef.current);
    }
    
    // 延迟搜索（降低到200ms提高响应速度）
    searchTimeoutRef.current = setTimeout(() => {
      searchStocks(newInputValue);
    }, 200);
  };

  // 处理选择
  const handleChange = (event, newValue) => {
    // 确保传递的是字符串
    let valueToSet = '';
    let selectedStock = null;
    
    if (typeof newValue === 'string') {
      valueToSet = newValue;
    } else if (newValue && typeof newValue === 'object' && newValue.code) {
      valueToSet = newValue.code;
      selectedStock = newValue;
    }
    
    onChange(valueToSet);
    if (valueToSet && onSelect) {
      onSelect(valueToSet);
    }
    
    // 添加到搜索历史
    if (selectedStock) {
      addToHistory(selectedStock);
    }
    
    setShowHistory(false);
    setShowTips(false);
  };

  // 处理搜索框聚焦
  const handleFocus = () => {
    if (!inputValue.trim()) {
      if (searchHistory.length > 0) {
        setShowHistory(true);
      } else {
        setShowTips(true);
      }
    }
  };

  // 处理搜索框失焦
  const handleBlur = () => {
    // 延迟隐藏，让用户有时间点击
    setTimeout(() => {
      setShowHistory(false);
      setShowTips(false);
    }, 200);
  };

  // 从历史记录中选择
  const selectFromHistory = (stock) => {
    setInputValue(stock.code);
    onChange(stock.code);
    if (onSelect) onSelect(stock.code);
    setShowHistory(false);
  };

  // 从热门股票中选择
  const selectFromPopular = (stock) => {
    setInputValue(stock.code);
    onChange(stock.code);
    if (onSelect) onSelect(stock.code);
    addToHistory(stock);
    setShowTips(false);
  };

  // 清理定时器
  useEffect(() => {
    return () => {
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
    };
  }, []);

  return (
    <Box sx={{ position: 'relative' }}>
      <Autocomplete
        value={value}
        onChange={handleChange}
        inputValue={inputValue}
        onInputChange={handleInputChange}
        options={options}
        loading={loading}
        freeSolo
        onFocus={handleFocus}
        onBlur={handleBlur}
        getOptionLabel={(option) => {
          if (typeof option === 'string') return option;
          if (option && typeof option === 'object' && option.code) {
            return option.name ? `${option.code} ${option.name}` : option.code;
          }
          return '';
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            label="股票代码或公司名称"
            placeholder="支持股票代码、公司名称、拼音搜索，如：000001、平安银行、pab"
            variant="outlined"
            fullWidth
            InputProps={{
              ...params.InputProps,
              startAdornment: (
                <Box sx={{ display: 'flex', alignItems: 'center', mr: 1 }}>
                  <SearchIcon color="action" />
                  <Tooltip title="查看搜索技巧">
                    <IconButton
                      size="small"
                      onClick={() => setShowTips(!showTips)}
                      sx={{ ml: 0.5 }}
                    >
                      <TipsIcon fontSize="small" color="primary" />
                    </IconButton>
                  </Tooltip>
                </Box>
              ),
            }}
            sx={{
              '& .MuiOutlinedInput-root': {
                '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                  borderColor: 'primary.main',
                  borderWidth: 2,
                }
              }
            }}
          />
        )}
        renderOption={(props, option) => {
          const { key, ...otherProps } = props;
          return (
            <Box component="li" key={key} {...otherProps}>
              <Box sx={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Typography variant="body1" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                    {option.code}
                  </Typography>
                  <Chip 
                    label={option.sector || '股票'} 
                    size="small" 
                    variant="outlined"
                    color={option.sector === '银行' ? 'primary' : 
                           option.sector === '科技' ? 'secondary' :
                           option.sector === '房地产' ? 'warning' : 'default'}
                    sx={{ fontSize: '0.75rem' }}
                  />
                </Box>
                <Typography variant="body2" color="text.secondary">
                  {option.name}
                </Typography>
              </Box>
            </Box>
          );
        }}
        noOptionsText={
          <Box sx={{ p: 2, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              未找到匹配的股票
            </Typography>
            <Typography variant="caption" color="text.secondary" sx={{ mt: 1, display: 'block' }}>
              尝试使用：股票代码、公司名称或拼音缩写
            </Typography>
          </Box>
        }
        loadingText={
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', p: 2 }}>
            <Typography variant="body2">智能搜索中...</Typography>
          </Box>
        }
        filterOptions={(x) => x} // 禁用内置过滤，使用后端搜索
      />
      
      {/* 搜索历史记录 */}
      {showHistory && searchHistory.length > 0 && (
        <Paper 
          sx={{ 
            position: 'absolute', 
            top: '100%', 
            left: 0, 
            right: 0, 
            zIndex: 1000, 
            mt: 1, 
            maxHeight: 300, 
            overflow: 'auto',
            bgcolor: 'background.paper',
            border: 1,
            borderColor: 'divider',
            boxShadow: 3,
          }}
        >
          <Box sx={{ p: 1, borderBottom: 1, borderColor: 'divider', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="subtitle2" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <HistoryIcon fontSize="small" />
              搜索历史
            </Typography>
            <Chip 
              label="清空" 
              size="small" 
              variant="outlined" 
              clickable
              onClick={clearHistory}
              sx={{ fontSize: '0.75rem' }}
            />
          </Box>
          {searchHistory.map((stock) => (
            <ListItem 
              key={stock.code} 
              button 
              onClick={() => selectFromHistory(stock)}
              sx={{ 
                py: 1,
                '&:hover': {
                  bgcolor: 'action.hover',
                }
              }}
            >
              <ListItemText
                primary={
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                      {stock.code}
                    </Typography>
                    <Chip 
                      label={stock.sector || '股票'} 
                      size="small" 
                      variant="outlined"
                      sx={{ fontSize: '0.7rem', height: 20 }}
                    />
                  </Box>
                }
                secondary={stock.name}
              />
              <ListItemSecondaryAction>
                <IconButton
                  edge="end"
                  size="small"
                  onClick={(e) => {
                    e.stopPropagation();
                    removeFromHistory(stock.code);
                  }}
                  sx={{ 
                    color: 'error.main',
                    '&:hover': {
                      bgcolor: 'error.light',
                      color: 'white',
                    }
                  }}
                >
                  <DeleteIcon fontSize="small" />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          ))}
        </Paper>
      )}
      
      {/* 搜索技巧和热门股票 */}
      {showTips && (
        <Paper 
          sx={{ 
            position: 'absolute', 
            top: '100%', 
            left: 0, 
            right: 0, 
            zIndex: 1000, 
            mt: 1, 
            maxHeight: 400, 
            overflow: 'auto',
            bgcolor: 'background.paper',
            border: 1,
            borderColor: 'divider',
            boxShadow: 3,
          }}
        >
          {/* 搜索技巧 */}
          <Box sx={{ p: 2, borderBottom: 1, borderColor: 'divider' }}>
            <Typography variant="subtitle2" sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
              <TipsIcon fontSize="small" color="primary" />
              智能搜索技巧
            </Typography>
            {searchTips.map((tip, index) => (
              <Box key={index} sx={{ mb: 1 }}>
                <Typography variant="caption" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                  {tip.type}:
                </Typography>
                <Typography variant="caption" sx={{ ml: 1, color: 'text.secondary' }}>
                  {tip.example} - {tip.desc}
                </Typography>
              </Box>
            ))}
          </Box>
          
          {/* 热门股票 */}
          <Box sx={{ p: 2 }}>
            <Typography variant="subtitle2" color="primary" gutterBottom>
              📈 热门股票推荐
            </Typography>
            {popularStocks.map((stock) => (
              <Box
                key={stock.code}
                onClick={() => selectFromPopular(stock)}
                sx={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  p: 1,
                  cursor: 'pointer',
                  borderRadius: 1,
                  '&:hover': {
                    bgcolor: 'action.hover',
                  }
                }}
              >
                <Box>
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                      {stock.code}
                    </Typography>
                    <Chip 
                      label={stock.sector} 
                      size="small" 
                      variant="outlined"
                      sx={{ fontSize: '0.7rem', height: 18 }}
                    />
                  </Box>
                  <Typography variant="caption" color="text.secondary">
                    {stock.name}
                  </Typography>
                </Box>
                <Typography variant="caption" color="text.secondary" sx={{ fontStyle: 'italic' }}>
                  {stock.tips}
                </Typography>
              </Box>
            ))}
          </Box>
        </Paper>
      )}
      
      {/* 搜索结果为空时的友好提示 */}
      <Box sx={{ mt: 2 }}>
        <Alert 
          severity="info" 
          sx={{ 
            bgcolor: 'primary.50',
            '& .MuiAlert-message': { fontSize: '0.875rem' }
          }}
        >
          <Typography variant="body2" sx={{ mb: 1 }}>
            <strong>💡 智能搜索已升级！</strong>
          </Typography>
          <Typography variant="caption" color="text.secondary">
            ✅ 支持拼音搜索（如：pab、zsyh、zmj）<br/>
            ✅ 支持模糊匹配和智能排序<br/>
            ✅ 支持行业关键词搜索（如：银行、科技）<br/>
            ✅ 自动保存搜索历史，快速复用
          </Typography>
        </Alert>
      </Box>
    </Box>
  );
};

export default StockSearch; 